import React from "react";
import { useParams, Link } from "react-router-dom";
import products from "./products";
import logo from "./logoCHPST.png";
import { useCurrency } from './context/CurrencyContext';
import { useMediaQuery } from "react-responsive"; // useMediaQuery import edildi
import Footer from './components/Footer'; // Footer bileeni import edildi

function ProductPage() {
  const { id } = useParams();
  const isMobile = useMediaQuery({ maxWidth: 768 }); // Mobil ekran kontrol

  const product = products.find((p) => p.id === id);

  const { selectedCurrency, setSelectedCurrency, exchangeRates, loadingRates, convertPrice } = useCurrency();

  if (!product) {
    return (
      <div style={{ color: "white", padding: 30 }}>
        <h2>Product not found</h2>
        <Link to="/" style={{ color: "#ffdb08" }}>Back to Home</Link>
      </div>
    );
  }

  return (
    <div style={{ padding: isMobile ? 16 : 30, color: "white", minHeight: '100vh', display: 'flex', flexDirection: 'column' }}> {/* Mobil iin padding azaltld, minHeight ve flex ayarlar eklendi */}
      {/* Balk ve Para Birimi Seici */}
      <div style={{
        display: "flex",
        flexDirection: isMobile ? "column" : "row", // Mobil iin dikey hizalama
        alignItems: isMobile ? "flex-start" : "center", // Mobil iin sola hizalama
        justifyContent: "space-between",
        marginBottom: isMobile ? 16 : 24, // Mobil iin margin azaltld
        gap: isMobile ? 16 : 0 // Mobil iin gap eklendi
      }}>
        {/* Logo ve Balk Link ile Sarmaland */}
        <Link to="/"
          style={{
            color: "white",
            display: "flex",
            alignItems: "center",
            gap: isMobile ? 8 : 10, // Mobil iin gap azaltld
            margin: 0,
            textDecoration: 'none'
          }}
        >
          <img src={logo} alt="Cheapest Logo" style={{ height: isMobile ? 32 : 40 }} /> {/* Mobil iin logo boyutu */}
          <span style={{ fontWeight: 700, fontSize: isMobile ? 24 : 28 }}>Cheapest</span> {/* Mobil iin font boyutu */}
          <span
            style={{
              fontWeight: 400,
              fontSize: isMobile ? 16 : 20, // Mobil iin font boyutu
              opacity: 0.7,
              marginLeft: isMobile ? 0 : 4, // Mobil iin margin kaldrld
              letterSpacing: 1,
            }}
          >
            Global
          </span>
        </Link>
        {/* Para Birimi Seici */}
        <div style={{ textAlign: isMobile ? 'left' : 'right', width: isMobile ? '100%' : 'auto' }}> {/* Mobil iin sola hizalama ve tam genilik */}
          <label htmlFor="currency-select" style={{ color: '#fff', marginRight: 8 }}>Para Birimi:</label>
          <select
            id="currency-select"
            value={selectedCurrency}
            onChange={(e) => setSelectedCurrency(e.target.value)}
            style={{
              padding: '8px',
              borderRadius: '4px',
              backgroundColor: '#333',
              color: '#ffdb08',
              border: '1px solid #555',
              fontSize: 16,
              width: isMobile ? 'auto' : 'auto' // Mobil iin genilik ayar
            }}
            disabled={loadingRates}
          >
            {loadingRates ? (
              <option>Loading...</option>
            ) : (
              Object.keys(exchangeRates).map((currency) => (
                <option key={currency} value={currency}>
                  {currency}
                </option>
              ))
            )}
          </select>
        </div>
      </div>


      <Link
        to="/"
        style={{
          background: "#ffdb08",
          color: "#222",
          fontWeight: "bold",
          textDecoration: "none",
          padding: "8px 18px",
          borderRadius: 8,
          display: "inline-block",
          marginBottom: 18,
        }}
      >
         Back
      </Link>
      {/* rn Resmi ve Detaylar */}
      <div style={{
        display: "flex",
        flexDirection: isMobile ? "column" : "row", // Mobil iin dikey hizalama
        gap: isMobile ? 24 : 32, // Mobil iin gap ayar
        alignItems: "flex-start",
        marginTop: 24,
        flexGrow: 1 // Ana ieriin footer' aa itmesini salar
      }}>
        <img
          src={product.image ? product.image : logo}
          alt={product.name}
          style={{
            height: isMobile ? 180 : 220, // Mobil iin resim boyutu
            objectFit: "contain",
            background: "#222",
            borderRadius: 12,
            padding: 12,
            maxWidth: isMobile ? '100%' : 'auto' // Mobil iin max genilik
          }}
        />
        <div style={{ width: isMobile ? '100%' : 'auto' }}> {/* Mobil iin tam genilik */}
          <h1 style={{ marginBottom: 12, fontSize: isMobile ? 20 : 24 }}>{product.name}</h1> {/* Mobil iin balk boyutu */}
          <h3 style={{ fontSize: isMobile ? 16 : 18 }}>Prices by Country</h3> {/* Mobil iin balk boyutu */}
          {/* Fiyat Tablosu */}
          <div style={{ overflowX: 'auto' }}> {/* Mobil iin yatay kaydrma */}
            <table style={{
              color: "white",
              background: "#222",
              borderRadius: 8,
              padding: 8,
              width: isMobile ? 'max-content' : '100%',
              borderCollapse: 'collapse' // ift kenarlklar kaldrr
            }}>
              <thead>
                <tr>
                  {/* Balk hcrelerine kenarlk eklendi */}
                  <th style={{ padding: 8, textAlign: 'left', border: '1px solid #444' }}>Country</th>
                  <th style={{ padding: 8, textAlign: 'left', border: '1px solid #444' }}>Price ({selectedCurrency})</th>
                  {/* "Original Currency" yerine "Original Price" yazld */}
                  <th style={{ padding: 8, textAlign: 'left', border: '1px solid #444' }}>Original Price</th>
                  <th style={{ padding: 8, textAlign: 'left', border: '1px solid #444' }}>Website</th>
                </tr>
              </thead>
              <tbody>
                {product.prices.map((price, idx) => (
                  <tr
                    key={idx}
                    style={{
                      backgroundColor: idx % 2 === 0 ? '#222' : '#2a2a2a' // ift/tek satr renkleri
                    }}
                  >
                    {/* Veri hcrelerine kenarlk eklendi */}
                    <td style={{ padding: 8, border: '1px solid #444' }}>{price.country}</td>
                    <td style={{ padding: 8, border: '1px solid #444' }}>{convertPrice(price.price_usd)} {selectedCurrency}</td>
                    {/* Original Currency yazs iin stil eklendi */}
                    <td style={{ padding: 8, border: '1px solid #444', fontSize: 12, opacity: 0.8 }}>
                      {price.price} {price.currency}
                    </td>
                    <td style={{ padding: 8, border: '1px solid #444', display: "flex", alignItems: "center", gap: 6 }}>
                      {price.company_logo && (
                        <img
                          src={price.company_logo}
                          alt={price.company}
                          style={{ width: 20, height: 20, borderRadius: 4, marginRight: 4 }}
                        />
                      )}
                      <span>{price.company}</span>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      </div>

      {/* Footer bileeni kullanld */}
      <Footer />
    </div>
  );
}

export default ProductPage;
