import React from "react";
import { HashRouter as Router, Route, Routes } from "react-router-dom"; // BrowserRouter yerine HashRouter kullanld
import HomePage from "./HomePage";
import ProductPage from "./ProductPage"; // ProductDetail yerine ProductPage import edildi
import { CurrencyProvider } from "./context/CurrencyContext"; // CurrencyProvider' import edin

function App() {
  return (
    <CurrencyProvider> {/* Uygulamay CurrencyProvider ile sarmalayn */}
      <Router> {/* BrowserRouter yerine HashRouter kullanld */}
        <Routes>
          <Route path="/" element={<HomePage />} />
          <Route path="/product/:id" element={<ProductPage />} /> {/* ProductDetail yerine ProductPage kullanld */}
          {/* Dier rotalarnz */}
        </Routes>
      </Router> {/* BrowserRouter yerine HashRouter kullanld */}
    </CurrencyProvider>
  );
}

export default App;
